
.uploadImg {
  width: 50%;
  margin: 0 auto;
  margin-top: 50px;
}
.uploadImg .imgContainer {
    position: relative;
    width: 300px;
    height: 300px;
    margin: 0 auto;
    border: 1px solid #ddd;
    margin-bottom: 30px;
}
.uploadImg .imgContainer .showClick, .uploadImg .imgContainer .upload, .uploadImg .imgContainer .showimg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
}
.uploadImg .imgContainer .showClick {
      z-index: 1;
      line-height: 300px;
}
.uploadImg .imgContainer .upload {
      opacity: 0;
      z-index: 10;
}
.uploadImg span {
    border: 1px solid #ddd;
}



